/* ÂÆûÁé∞ÁöÑÂá†‰∏™ DOM Êé•Âè£
** dom.create(`<div>hi</div>`) // Áî®‰∫éÂàõÂª∫ËäÇÁÇπ
** dom.after(node, node2) // Áî®‰∫éÂêëÂêéËøΩÂä†ÂÖÑÂºüËäÇÁÇπ
// ÂéüÁîüÁöÑÊèê‰æõ‰∫Ü‰∏Ä‰∏™ÂÖºÂÆπÊÄß‰∏ç‰Ω≥ÁöÑÂÆûÈ™åÊÄßÊé•Âè£`ChildNode.after() MDN`
** dom.before(node, node2) // Áî®‰∫éÂêëÂâçËøΩÂä†ÂÖÑÂºüËäÇÁÇπ
** dom.append(parent, child) // Áî®‰∫éÂàõÂª∫Â≠êËäÇÁÇπ
** dom.wrap(`<div></div>`) //  Áî®‰∫éÂàõÂª∫Áà∂ËäÇÁÇπ
*/
// dom.create = function() {}
window.dom = {
    // create: function() {} // ÂèØÁÆÄÂåñ‰∏∫
    create(tagName /* ËØ≠‰πâÂåñ ÂΩ¢ÂèÇ */ ) {
        // return document.createElement(tagName) // ‰∏çËÉΩÂàõÂª∫Â∏¶ÊúâÁªìÊûÑÁöÑ HTML ÂÖÉÁ¥†`<div><span>1</span></div>`

        // const container = document.createElement("div")
        const container = document.createElement("template")
        container.innerHTML = tagName.trim() // Èô§ÂéªÁ©∫Ê†º
            // return container.children[0]
        return container.content.firstChild
            /* Â≠òÂú® ‰∏çÂèØËØÜÂà´ÂÖÉÁ¥†(<td></td>)ÁöÑ bug
             ** <td</td>> ‰∏çËÉΩÂçïÁã¨Â≠òÂú® Âè™ËÉΩÊîæÂú®<table></table> Èáå<tr></tr>Êàñ<tbody></tbody> ÈáåÔºåÊîæÂú® div Èáå‰∏çÁ¨¶Âêà HTML ËØ≠Ê≥ï
             ** ÂèØ‰ª•Êîæ‰ªªÊÑèÂÖÉÁ¥†Ôºå‰∏çÂá∫ bug ÁöÑÊ†áÁ≠æÊòØ <template></template>
             ** <template></template> ÊòØÁ™úÈó®Áî®Êù•ÂÆπÁ∫≥‰∫∫‰ª•Ê†áÁ≠æÁöÑ
             ** <template></template> Áî®template.content.firstChildÊãøÂà∞
             */
    },
    after(node, node2) { // Âú®ÂêéÈù¢ÊèíÂÖ•ËäÇÁÇπÔºåÂ∞±Áõ∏ÂΩì‰∫éÂú®Ê≠§ node ÂêéÈù¢ÁöÑËäÇÁÇπÁöÑÂâçÈù¢Êèí // ÂøÖÈ°ªË∞ÉÁî®Áà∂ËäÇÁÇπÁöÑ insertBefore() ÊñπÊ≥ï
        console.log(node.siblings) // null ?
        node.parentNode.insertBefore(node2, node.nextSibling)
            /* Âà§Êñ≠ ÊéíÈô§ÊúÄÂêé‰∏Ä‰∏™ËäÇÁÇπ Ê≤°Êúâ‰∏ã‰∏Ä‰∏™ËäÇÁÇπ null ‰πüÁ¨¶Âêà */
    },
    before(node, node2) {
        node.parentNode.insertBefore(node2, node)
    },
    append(parent, node) {
        parent.appendChild(node)
    },
    wrap(node, parent) {
        dom.before(node, parent) // Â∞ÜË¶ÅÂåÖË£πÁöÑ‚ÄúÁà∂ËäÇÁÇπ‚ÄùÂÖàÊèíÂà∞ÁõÆÊ†áËäÇÁÇπÁöÑÂâçÈù¢
        dom.append(parent, node) // ÂÜçÊääÁõÆÊ†áËäÇÁÇπÁî® append ÁßªËá≥Â∞ÜË¶ÅÂåÖË£πÁöÑÁà∂ËäÇÁÇπÁöÑ‰∏ãÈù¢
    },
    remove(node) {
        // node.remove() // IE ‰∏çÊîØÊåÅ ÂÖºÂÆπÊÄß‰∏çÂ•Ω
        node.parentNode.removeChild(node)
        return node // ‰ªçÁÑ∂ÈúÄË¶ÅËé∑ÂèñÊ≠§ËäÇÁÇπÁöÑÂºïÁî®
    },
    empty(node) { // Ê∏ÖÁ©∫ node ÈáåÈù¢ÁöÑÊâÄÊúâÂ≠êÂÖÉÁ¥†
        // node.innerHTML = ''
        // const childNodes = node.childNodes ÂèØ‰ª•ÊîπÂÜôÊàê‰ª•‰∏ãÁöÑÂÜôÊ≥ï
        /*     const {
                childNodes
            } = node */ // Ëß£ÊûÑËµãÂÄº 
        const array = []
            /* 
            for (let i = 0; i < childNodes.length; i++) { // ‰∏çÈúÄË¶Åi++ÁöÑÂæ™ÁéØÂ∞±Áî® while Âæ™ÁéØ‰ª£Êõø
                console.log(childNodes)
                console.log(childNodes.length)
                dom.remove(childNodes[i]) // remove( nodes) ‰ºöÂÆûÊó∂ÊîπÂèò nodes ÁöÑÈïøÂ∫¶ÊØèÊ¨°Âáè‰∏Ä ÂØºËá¥Âæ™ÁéØÁöÑÈïøÂ∫¶‰∏çÂõ∫ÂÆö Âá∫Áé∞ bug
                array.push(childNodes[i])
            } 
            */
            //  ‰∏çÈúÄË¶Åi++ÁöÑÂæ™ÁéØÂ∞±Áî® while Âæ™ÁéØ‰ª£Êõøfor Âæ™ÁéØ

        /* Ëé∑ÂèñÁ¨¨‰∏Ä‰∏™Â≠êËäÇÁÇπ Âπ∂ push ËøõÊï∞ÁªÑ */
        let x = node.firstChild
        while (x) { // Â¶ÇÊûú x Â≠òÂú®
            array.push(dom.remove(node.firstChild))
            x = node.firstChild // Á¨¨‰∏Ä‰∏™Â≠êËäÇÁÇπÂ∑≤ÁªèÁßªÈô§ ÂéüÂÖàÁ¨¨‰∫åËäÇÁÇπÂ∞±Âèò‰∏∫Áé∞Âú®ÁöÑÁ¨¨‰∏Ä‰∏™ËäÇÁÇπ
        }
        return array // ‰ªçÁÑ∂ÈúÄË¶ÅËé∑ÂèñÊ≠§ËäÇÁÇπÁöÑÂºïÁî®
    },
    /* Êîπ */
    attr(node, name, value) { // ÁªÑÂêà
        node.setAttribute(name, value) // ÂéüÁîü setAttribute(name, value)
    }
}